@use 'styles/mixins' as m;
@use 'styles/variables' as v;

@import '../../../styles/function.scss';

.title{
    @include m.text-character(16px,29px,v.$gray-4-c);
    text-transform: uppercase;
    margin-bottom: 18px;
}

.list{
    @include m.flexibility(column,center,flex-start);
    gap: 24px;
}

.subtitle{
    @include m.text-character(14px, 29px, v.$gray-4-c);
    font-weight: 600;
    margin-bottom: 12px;
}

.text{
    @include m.text-character(14px, 29px, v.$gray-3-c);
}

// Desktop styles
@media screen and (min-width: v.$b-tablet-m) {
    .title {
        @include m.text-character(calcOutValue(24), calcOutValue(40), v.$gray-4-c);
        margin-bottom: calcOutValue(26);
    }

    .list {
        gap: calcOutValue(36);
    }

    .subtitle {
        @include m.text-character(calcOutValue(22), calcOutValue(36), v.$gray-4-c);
        margin-bottom: calcOutValue(16);
    }

    .text {
        @include m.text-character(calcOutValue(22), calcOutValue(36), v.$gray-2-c);
    }
}